					<el-input v-model="ruleForm.zixunbianhao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="" clearable  :readonly="ro.yonghuzhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="" clearable  :readonly="ro.yonghuxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="zhenduanjieguo" >
					<el-input v-model="ruleForm.zhenduanjieguo" placeholder="" clearable  :readonly="ro.zhenduanjieguo"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="zhenduanjieguo" >
					<el-input v-model="ruleForm.zhenduanjieguo" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yongyaoyijian" >
					<el-input v-model="ruleForm.yongyaoyijian" placeholder="" clearable  :readonly="ro.yongyaoyijian"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yongyaoyijian" >
					<el-input v-model="ruleForm.yongyaoyijian" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="jilushijian" >
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.jilushijian" 
						type="datetime"
						:readonly="ro.jilushijian"
						placeholder=""
					></el-date-picker>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.jilushijian" label="" prop="jilushijian" >
					<el-input v-model="ruleForm.jilushijian" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yishengzhanghao" >
					<el-input v-model="ruleForm.yishengzhanghao" placeholder="" clearable  :readonly="ro.yishengzhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yishengzhanghao" >
					<el-input v-model="ruleForm.yishengzhanghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yishengxingming" >
					<el-input v-model="ruleForm.yishengxingming" placeholder="" clearable  :readonly="ro.yishengxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yishengxingming" >
					<el-input v-model="ruleForm.yishengxingming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.touxiang" label="" prop="touxiang" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
						@change="touxiangUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.touxiang" label="" prop="touxiang" >
					<img v-if="ruleForm.touxiang.substring(0,4)=='http'&&ruleForm.touxiang.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.touxiang" width="100" height="100">
					<img v-else-if="ruleForm.touxiang.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.touxiang.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.touxiang.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
			</template>
			<el-form-item class="textarea" v-if="type!='info'" label="" prop="yishengjianyi" >
				<el-input
					style="min-width: 200px; max-width: 600px;"
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.yishengjianyi" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.yishengjianyi" label="" prop="yishengjianyi" >
				<span class="text">{{ruleForm.yishengjianyi}}</span>
			</el-form-item>
			<el-form-item class="textarea" v-if="type!='info'" label="" prop="zhuyishixiang" >
				<el-input
					style="min-width: 200px; max-width: 600px;"
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.zhuyishixiang" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.zhuyishixiang" label="" prop="zhuyishixiang" >
				<span class="text">{{ruleForm.zhuyishixiang}}</span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	export default {
		data() {
			return {
				id: '',
				type: '',
			
			
				ro:{
					zixunbianhao : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
					zhenduanjieguo : false,
					yongyaoyijian : false,
					yishengjianyi : false,
					zhuyishixiang : false,
					jilushijian : false,
					yishengzhanghao : false,
					yishengxingming : false,
					touxiang : false,
					userid : false,
				},
			
				ruleForm: {
					zixunbianhao: this.getUUID(),
					yonghuzhanghao: '',
					yonghuxingming: '',
					zhenduanjieguo: '',
					yongyaoyijian: '',
					yishengjianyi: '',
					zhuyishixiang: '',
					jilushijian: '',
					yishengzhanghao: '',
					yishengxingming: '',
					touxiang: '',
					userid: '',
				},

				rules: {
					zixunbianhao: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
					zhenduanjieguo: [
					],
					yongyaoyijian: [
					],
					yishengjianyi: [
					],
					zhuyishixiang: [
					],
					jilushijian: [
					],
					yishengzhanghao: [
					],
					yishengxingming: [
					],
					touxiang: [
					],
					userid: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.jilushijian = this.getCurDateTime()
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='zixunbianhao'){
							this.ruleForm.zixunbianhao = obj[o];
							this.ro.zixunbianhao = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
						if(o=='zhenduanjieguo'){
							this.ruleForm.zhenduanjieguo = obj[o];
							this.ro.zhenduanjieguo = true;
							continue;
						}
						if(o=='yongyaoyijian'){
							this.ruleForm.yongyaoyijian = obj[o];
							this.ro.yongyaoyijian = true;
							continue;
						}
						if(o=='yishengjianyi'){
							this.ruleForm.yishengjianyi = obj[o];
							this.ro.yishengjianyi = true;
							continue;
						}
						if(o=='zhuyishixiang'){
							this.ruleForm.zhuyishixiang = obj[o];
							this.ro.zhuyishixiang = true;
							continue;
						}
						if(o=='jilushijian'){
							this.ruleForm.jilushijian = obj[o];
							this.ro.jilushijian = true;
							continue;
						}
						if(o=='yishengzhanghao'){
							this.ruleForm.yishengzhanghao = obj[o];
							this.ro.yishengzhanghao = true;
							continue;
						}
						if(o=='yishengxingming'){
							this.ruleForm.yishengxingming = obj[o];
							this.ro.yishengxingming = true;
							continue;
						}
						if(o=='touxiang'){
							this.ruleForm.touxiang = obj[o];
							this.ro.touxiang = true;
							continue;
						}
						if(o=='userid'){
							this.ruleForm.userid = obj[o];
							this.ro.userid = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.yishengzhanghao!=''&&json.yishengzhanghao) || json.yishengzhanghao==0) && this.$storage.get("role")!=""){
							this.ruleForm.yishengzhanghao = json.yishengzhanghao
							this.ro.yishengzhanghao = true;
						}
						if(((json.yishengxingming!=''&&json.yishengxingming) || json.yishengxingming==0) && this.$storage.get("role")!=""){
							this.ruleForm.yishengxingming = json.yishengxingming
							this.ro.yishengxingming = true;
						}
						if(((json.touxiang!=''&&json.touxiang) || json.touxiang==0) && this.$storage.get("role")!=""){
							this.ruleForm.touxiang = json.touxiang
							this.ro.touxiang = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `zixunjilu/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.zixunbianhao) {
						this.ruleForm.zixunbianhao = String(this.ruleForm.zixunbianhao)
					}
					if(this.ruleForm.touxiang!=null) {
						this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `zixunjilu/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.zixunjiluCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.zixunjiluCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			touxiangUploadChange(fileUrls) {
				this.ruleForm.touxiang = fileUrls;
			},
		}
	};
</script>
